<template>
  <div class="section page1">
    <v-ba/>
    <div class="slideshow">
      <div class="slideshow-image"></div>
      <div class="slideshow-image"></div>
      <div class="slideshow-image"></div>
      <div class="slideshow-image"></div>
    </div>
    <div class="awt">
      <img src="../../../assets/awt.jpg">
    </div>
    <div :class="['conent','animated',swiperIndex===0 ? 'bounceInDown' : '']" ref="contWidth"
         @mouseenter="Menter"
         @mouseleave="Mleave"
         @mousemove="Mmove"
         :style="{transform:`scale(1.03) translate(${x}px,${y}px) rotateX(${x}deg) rotateY(${y}deg)`}">
      <div class="text" :class="[{shadow:flag}]">
        <h2>Hello, this  is  me.</h2>
        <strong>Life is more than just now, and poetry and distance</strong>
        <p class="W-p1">我叫苏同辉</p>
        <p class="W-p2">一名前端开发工程师</p>
        <p class="W-mailbox" style="color:rgb(228, 144, 109)">1193101613@qq.com</p>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import ba from '../backColor/ba'
  import {mapState} from 'vuex'
  export default {
    name: "page1",
    data() {
      return{
        flag:false,
        x:null,
        y:null,
        active:false,
        index:1
      }
    },
    methods:{
      Menter(){
        this.flag=true
        setTimeout(e=>{
          this.active=false
        })
      },
      Mleave(){
        this.flag=false
        this.x = 0;
        this.y = 0;
        this.active=true
      },
      Mmove(e){
        let t = e.currentTarget;
        let [x,y,tx,ty,w,h]=[e.clientX,e.clientY,t.offsetLeft,t.offsetTop,t.clientWidth,t.clientHeight];
        this.x = (x-(tx+w/2))/25;
        this.y = (y-(ty+h/2))/25;
      },
    },
    computed: {
      ...mapState([
        'swiperIndex',
      ]),
    },
    components:{
      "v-ba": ba
    }
  }
</script>

<style lang="less" rel="stylesheet/less">
  @import "../../../assets/uts.less";
  .page1{
    .slideshow{
      div{
        .loop(4);
      }
    }
    .awt{
      display: none;
      width: 160px;
      height: 170px;
      position: absolute;
      z-index: 5;
      left: 50%;
      top: 20%;
      margin-left: -75px;
      border: 1px solid #ccc;
      border-radius: 50%;
      img{
        display: block;
        width: 100%;
        border-radius: 50%;
        height: 100%;
      }
    }
    .conent{
      transition: .4s all;
      position: absolute;
      left: calc(50% - 350px);
      top:calc(50% - 140px);
      z-index: 20;
      display: flex;
      width: 700px;
      height: 280px;
      text-align: center;
      background: transparent;
      border-radius: 10px;
      .text{
        width: 100%;
        &.shadow{
          box-shadow: 0 0 70px #111;
        }
        p {
          color: #fff;
          font-size: 20px;
          margin-top: 8px;
        }
        h2{
          font-size: 65px;
          font-family: "HelveticaNeue";
        }
        strong{
          font-size: 25px;
          color: #cec1c1;
        }
      }
    }
  }
  @import "../../../assets/media.less";
</style>



