<template>
  <div class="section page4">
    <v-ba></v-ba>
    <div class="about"><span>我的经历</span></div>
    <div :class="['cont','animated',swiperIndex===3 ? 'bounceInUp' : '']">
      <div v-for="(list,index) in PageList" class="cont-detail">
        <div class="years" @mouseenter="enter(index)" :class="[{rotate:index === isShow}]"><span>{{index}}</span> <b>Year</b></div>
        <transition name="show">
          <div v-if="isShow === index" class="div">{{index}}</div>
        </transition>
        <div class="big">
          <div v-for="(group,index) in list" class="flexTable">
            <div class="time">
            <span v-for="(item,index) in group.time">
              {{item}}
            </span>
            </div>
            <span class="radio"></span>
            <div class="conts">
              <h2 class="name">
                {{group.item[0].name}}
              </h2>
              <p class="obj">
                {{group.item[1].obje}}
              </p>
              <p>
                {{group.item[2].act}}
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import ba from '../backColor/ba'
  import {mapState} from 'vuex'
  export default {
    name: "page4",
    data(){
      return {
        isShow:0,
        PageList: {
          "2017":[
            {time:["2017年4月","至","2017年9月"],item:[{name:'淄博科里特网络'},{obje:"网络部/网站运营"},{act:"开始全面接触前端,工作比较简单写写静态页面,利用班后时间学习js"}]},
            {time:["2017年9月","至","2018年6月"],item:[{name:'66公里技术'},{obje:"技术部/前端工程师"},{act:"利用vue全家桶负责公司移动端,PC端,后台管理的前端开发,6月因个人原因从公司离职"}]},
          ],
          "2018":[
            {time:["2018年6月","至","至今"],item:[{name:'源创软件'},{obje:"技术部/前端工程师"},{act:"负责前端开发,联调后端接口,负责项目流程的进度,代码修改等"}]}
          ]
        }
      }
    },
    methods:{
      enter(index){
        this.isShow = index
      },
    },
    computed: {
      ...mapState([
        'swiperIndex',
      ]),
    },
    components:{
      "v-ba": ba
    },
  }
</script>

<style lang="less" rel="stylesheet/less">
  .page4{
    background: url("../../../assets/page4.jpg") no-repeat 50% 50%;
    .about{
      position: absolute;
      z-index: 9;
      left: 50%;
      top: 15%;
      margin-left: -35px;
      font-size: 25px;
      text-align: justify;
      justify-content:center;
      display: flex;
      span{
        position: relative;
        &:after{
          position: absolute;
          content: '';
          height: 2px;
          width: 100%;
          background: #06b5de;
          bottom: -10px;
          left: 0;
        }
      }
    }
    .cont{
      position: absolute;
      top: 25%;
      z-index: 99;
      left: 50%;
      margin-left: -350px;
      width: 700px;
    }
    .cont-detail{
      position: relative;
      overflow: hidden;
      .years{
        transition: .5s all;
        width: 83px;
        height: 83px;
        font-size: 30px;
        font-weight: bold;
        text-align: center;
        color: #6bc30d;
        top: 0;
        border-radius: 50%;
        border: 5px solid #d7d8d8;
        cursor: pointer;
        margin-left: 70px;
        position: relative;
        &.rotate{
          transform: rotate(360deg);
        }
        span{
          display: block;
          width: 100%;
          margin-top: 18px;
        }
        b{
          font-size: 18px;
          color: #ffd200;
          display: block;
        }
      }
      .div{
        width: 83px;
        height: 83px;
        background: #6bc30d;
        position: absolute;
        border-radius: 50%;
        margin-left: 75px;
        top: 5px;
        text-align: center;
        line-height: 83px;
        font-size: 25px;
      }
      .big{
        margin-top: 10px;
        .flexTable{
          align-items: center;
          display: flex;
          line-height: 2;
          height: 110px;
          cursor: pointer;
          &:hover{
            .conts{
              .name{
                color: #6bc30d;
              }
            }
            .radio{
              border-width: 5px;
              left: 103px;
            }
          }
          .time{
            width: 120px;
            text-align: center;
            position: relative;
            &:after{
              position: absolute;
              content: "";
              width: 25px;
              height: 2px;
              background: #6bc30d;
              right: 0;
              top: 35px;
            }
            span{
              display: block;
              font-size: 12px;
              font-style: normal;
              color: #e6de8d;
            }
          }
          .conts{
            margin-left: 20px;
            &:before{
              position: absolute;
              content: "";
              height: 100%;
              left: 111px;
              border: 5px solid #d7d8d8;
              margin-top: -21px;
            }
            .name{
              transition: color 0.3s ease-in;
              color: rgb(255, 255, 255);
              font-size: 24px;
            }
            .obj{
              display: block;
              margin: -2px 0 2px;
              color: #8ca870;
            }
          }
          .radio{
            transition: all .3s;
            border: 3px solid #a6db6d;
            height: 15px;
            width: 15px;
            border-radius: 50%;
            background: #fff;
            display: block;
            position: absolute;
            cursor: pointer;
            left: 106px;
            z-index: 9;
          }
        }
      }
    }
    .show-enter-active,.show-leave-active{
      transition: all 0.5s;
      opacity: 1;
      transform: rotate(360deg);
    }
    .show-enter,.show-leave-active{
      opacity: 0;
      transform: rotate(-360deg);
    }
  }
  @import "../../../assets/media.less";
</style>



