<template>
  <div class="section page2">
    <v-ba></v-ba>
    <div class="about">
     <span>关于我</span>
    </div>
    <div :class="['center','animated',swiperIndex===1 ? 'zoomInUp' : '']">
      <p>
        您好，我叫苏同辉，坐标济南。
        我并不是计算机专业,但对前端很感兴趣，所以自学前端，今后的职业规划是朝着全栈发展，目前已经会了vue可以熟练上手,es6常用方法已经了解,css使用less,正在学习react全家桶。
        在工作两年以来,本人在公司主要负责整个页面的前端开发，配合后台人员实现产品前端界面效果与功能。写过响应式页面，移动端页面，pc端页面，后台管理,混合App。
      </p>
      <p>
        不浮躁，喜欢前端，执行力，学习能力都棒棒哒,喜欢瞎折腾一些新东西。
      </p>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import ba from '../backColor/ba'
  import {mapState} from 'vuex'
  export default {
    components:{
      "v-ba": ba
    },
    computed: {
      ...mapState([
        'swiperIndex',
      ]),
    },
  }
</script>

<style lang="less" rel="stylesheet/less">
  .page2{
    background: url("../../../assets/bg2-2.jpg") no-repeat 50% 50%;
    .about{
      position: absolute;
      z-index: 9;
      left: 50%;
      top: 20%;
      margin-left: -35px;
      font-size: 25px;
      text-align: justify;
      justify-content:center;
      display: flex;
      span{
        position: relative;
        &:after{
          position: absolute;
          content: '';
          height: 2px;
          width: 100%;
          background: #06b5de;
          bottom: -10px;
          left: 0;
        }
      }
    }
    .center{
      position: absolute;
      font-size: 20px;
      z-index: 9;
      max-width: 700px;
      margin-left: -350px;
      left: 50%;
      line-height: 2;
      top: calc(60% - 200px);
      p{
        text-indent: 2.4rem;
        font-size: 17px;
        color: #fff;
        text-align: justify;
      }
    }
  }
  @import "../../../assets/media.less";
</style>



