<template>
  <div>
    <div class="tabBtn" @click="navTab">
      <div class="speech-bubble" v-show="navToggle">
        <p>导航</p>
      </div>
      <i class="fa" aria-hidden="true" :class="navToggle? 'fa-times' :'fa-bars'"></i>
    </div>
    <div class="pageNav" :class="[{right:!navToggle}]">
      <ul id="menu" :class="[{right:!navToggle}]">
        <li v-for="(list,index) in list" @click="move(index)" class="menuLi" @mouseenter="enter(list)" @mouseleave="leave(list,index)">
          <a>{{list.name}} <span :class="[{active:index === swiperIndex || list.active}]"></span></a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import {mapState} from 'vuex'
  import {mapMutations} from 'vuex'
  export default {
    name: "navTab",
    data() {
      return {
        list: [
          {name:"首页"},{name:"关于我"},{name:'技能掌握'},{name:'我的作品'},{name:'联系我'}
        ],
      }
    },
    props:{
      refs:{
        type:Object
      }
    },
    computed: {
      ...mapState([
        'navToggle',
        'swiperIndex'
      ]),
    },
    methods: {
      ...mapMutations([
        'toggle',
        'swiperIndexAction'
      ]),
      enter(item){
        this.$set(item,'active',true)
      },
      leave(item){
        this.$set(item,'active',false)
      },
      navTab(){
        this.toggle(true)
      },
      move(index){
        this.refs.fullpage.api.moveTo(index+1)
        this.swiperIndexAction(index)
        this.toggle(false)
      }
    }
  }
</script>

<style lang="less" rel="stylesheet/less">
  .tabBtn{
    position: fixed;
    text-align: center;
    top: 20px;
    width: 65px;
    height: 65px;
    border-radius: 50%;
    cursor: pointer;
    -webkit-box-shadow: 0 3px 0 rgba(0, 0, 0, 0.2);
    box-shadow: 0 3px 0 rgba(0, 0, 0, 0.2);
    right: 40px;
    z-index: 9;
    color: #fff;
    line-height: 65px;
    i{
      font-size: 28px;
      vertical-align: middle;
    }
  }
  .pageNav{
    letter-spacing: 1px;
    position: fixed;
    z-index: 7;
    right: 0;
    top: 0;
    width: 30%;
    height: 0;
    transition: .2s all;
    border-top: none;
    border-bottom: #e4e2e3 solid;
    border-left: transparent 180px solid;
    border-right: none;
    box-sizing: border-box;
    font-size: 1.1em;
    border-bottom-width: 949px;
    &.right{
      right:-100%
    }
    #menu{
      transition: .2s all;
      position: fixed;
      top: 50%;
      display: block;
      right: 30px;
      margin-top: -115px;
      color: #000;
      font-size: 18px;
      &.right{
        right:-100%
      }
      .menuLi{
        font-size: 20px;
        cursor: pointer;
        line-height: 50px;
        text-align: right;
        white-space: nowrap;
        a{
          position: relative;
          display: inline-block;
          user-select: none;
          padding: 10px 30px;
          span{
            position: absolute;
            height: 1.5px;
            background: #337ab7;
            width: 0;
            -webkit-transition: 0.4s all;
            transition: 0.2s all;
            display: block;
            margin: 0 auto;
            left: 0px;
            right: 0px;
            top: 34px;
            &.active{
              width: 100%;
            }
          }
        }

      }
    }
  }
  .speech-bubble{
    font-size: 16px;
    position: absolute;
    background-color: #292929;
    width: 65px;
    color: white;
    text-align: center;
    border-radius: 6px;
    top: 85px;
    height: 30px;
    line-height: 30px;
    &:after{
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      bottom: 100%;
      left: 50%;
      margin-left: -10px;
      border: 10px solid;
      border-bottom-color: #292929;
      border-top-color: transparent;
      border-left-color: transparent;
      border-right-color: transparent;
    }
  }

</style>



