<template>
  <div class="section page3">
    <v-ba></v-ba>
    <div class="about"><span>我的作品</span></div>
    <div :class="['swiper','animated',swiperIndex===2 ? 'rotateOut' : '']">
      <swiper :options="swiperOption" ref="mySwiper">
        <swiper-slide v-for="(item,index) in list">
          <a :href="item.href">
            <img :src="item.name">
            <p>{{item.act}}</p>
          </a>
        </swiper-slide>
      </swiper>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import ba from '../backColor/ba'
  import con1 from "../../../assets/01.jpg";
  import con2 from "../../../assets/02.jpg";
  import con3 from "../../../assets/03.jpg";
  import con4 from "../../../assets/04.jpg";
  import con5 from "../../../assets/05.jpg";
  import con6 from "../../../assets/06.png";
  import con7 from "../../../assets/07.png";
  import {mapState} from 'vuex'
  export default {
    data(){
      return{
        list:[
          {name:con1,act:"汽车部件查询后台管理",href:'http://jmanagerkowlege.zc.66km.cn'},
          {name:con2,act:"汽车部件查询(移动端)",href:'http://jkowlege.zc.66km.cn'},
          {name:con6,act:"electron+vue网易云",href:''},
          {name:con3,act:"漫画web端",href:'https://github.com/sth1193101613/webcomic'},
          {name:con4,act:"漫画移动端",href:'https://github.com/sth1193101613/comic'},
          {name:con5,act:"进销存管理系统",href:'http://jinxiaocun.zc.66km.cn'},

          {name:con7,act:"电报系统",href:''}
          ],
        swiperOption:{
          effect : 'cube',
          loop:true,
          cubeEffect: {
            slideShadows: true,
            shadow: true,
            shadowOffset: 100,
            shadowScale: 0.6,
          },
        },
      }
    },
    computed: {
      ...mapState([
        'swiperIndex',
      ]),
    },
    components:{
      "v-ba": ba
    },
  }
</script>

<style lang="less" rel="stylesheet/less">
  .page3{
    background: url("../../../assets/page3.jpg") no-repeat 50% 50%;
    .about{
      position: absolute;
      z-index: 9;
      left: 50%;
      top: 15%;
      margin-left: -35px;
      font-size: 25px;
      text-align: justify;
      justify-content:center;
      display: flex;
      span{
        position: relative;
        &:after{
          position: absolute;
          content: '';
          height: 2px;
          width: 100%;
          background: #06b5de;
          bottom: -10px;
          left: 0;
        }
      }
    }
    .swiper{
      position: absolute;
      top: 30%;
      z-index: 99;
      width: 100%;
      left: 50%;
      margin-left: -350px;
      max-width: 700px;
      img{
        width: 100%;
        display: block;
      }
      p{
        line-height: 2;
        font-size: 20px;
        text-align: center;
        background: rgba(0,0,0,03);
        color: #dcd3d3;
      }
    }
  }

  @import "../../../assets/media";
</style>



