import Vue from 'vue'
import Router from 'vue-router'
import Myresume from '@/components/fullpage/Myresume'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Myresume',
      component: Myresume
    }
  ]
})



