<template>
  <div class="section page5">
    <v-ba></v-ba>
    <div class="about"><span>联系我</span></div>
    <div ref="box" :class="['pageCont','animated',swiperIndex===4 ? 'swing' : '']">
      <div class="posT"></div>
      <div class="posR"></div>
      <h2 class="title">CONTACTFOR ME</h2>
      <ul class="ul">
        <li v-for="(item,index) in list" :style="{color:item.color}" class="list">{{item.title}}</li>
      </ul>
      <div class="titl">
        <p> 学习是一个充实愉悦的过程 </p>
        <p> 喜欢尝试，期待新鲜事物 </p>
        <p> 前端即兴趣，兴趣即未来 </p>
        <p>  行路有良友，便是捷径 </p>
        <p>  期待有机会与您共事！ </p>
      </div>
      <div class="posB"></div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import ba from '../backColor/ba'
  import {mapState} from 'vuex'
  export default {
    name: "page5",
    data() {
      return {
        boxWidth: '',
        list: [{title: '灵感', color: "#e3723c"}, {title: '代码', color: "#84c800"}, {title: '梦想', color: "#33ccff"}, {title: '未来', color: "#fffff"}]
      }
    },
    computed: {
      ...mapState([
        'swiperIndex',
      ]),
    },
    components: {
      "v-ba": ba
    },
  }
</script>

<style lang="less" rel="stylesheet/less">
  .page5{
    background: url("../../../assets/page5.jpg") no-repeat 50% 50%;
    .about{
      position: absolute;
      z-index: 9;
      left: 50%;
      top: 15%;
      margin-left: -35px;
      font-size: 25px;
      text-align: justify;
      justify-content:center;
      display: flex;
      span{
        position: relative;
        &:after{
          position: absolute;
          content: '';
          height: 2px;
          width: 100%;
          background: #06b5de;
          bottom: -10px;
          left: 0;
        }
      }
    }
    .pageCont{
      position: absolute;
      top: 25%;
      z-index: 99;
      left: 50%;
      margin-left: -350px;
      width: 700px;
      .title{
        top: 40px;
        font-family:'ACMEExplosive';
        font-size: 40px;
        color: #ffd200;
        text-align: center;
        position: relative;
        &:after{
          position: absolute;
          content: "";
          height: 2px;
          background: #fff;
          left: 25%;
          bottom: -10px;
          width: 50%;
        }
      }
      .list{
        font-size: 24px;
        display: inline-block;
        text-align: center;
      }
      ul{
        margin-top: 90px;
      }
      .posT{
        position: absolute;
        right: 0;
        border-top: 6px solid #ffd200;
        border-right: 6px solid #ffd200;
        width: 150px;
        height: 100px;
        top: -20px;
      }
      .posB{
        width: 688px;
        height: 30px;
        border-bottom: 6px solid #ffd200;
        border-right: 6px solid #ffd200;
        border-left: 6px solid #ffd200;
        position: absolute;
        bottom: 0;
      }
      .ul{
        display: flex;
        li{
          flex: 1;
        }
      }
      .titl{
        padding: 20px 0;
        p{
          text-align: center;
          font-size: 18px;
          color: #fff;
          line-height: 40px;
        }
      }
      .posR{
        position: absolute;
        left: 0;
        border-top: 6px solid #ffd200;
        border-left: 6px solid #ffd200;
        width: 150px;
        height: 100px;
        top: -20px;
      }
    }
  }
</style>



