<template>
 <div class="audiosSwiper">
   <div class="audios" :class="[{dot:audioPlay}]"  @click="autoPlay">
     <div class="audios1" :class="[{dot2:audioPlay}]">
       <div class="audios2" :class="[{dot3:audioPlay}]">
         <audio hidden="true"
                loop
                controls="controls"
                ref="audio">
           <source  src="../../assets/O5QEAFiWsNqAAyGVACr_hEQPYyI883.mp3" type="audio/mpeg">
         </audio>
         <input type="button" value="播放" class="btn"/>
         <i class="fa" aria-hidden="true" :class="audioPlay?'fa-pause':'fa-play'"></i>
       </div>
     </div>
   </div>
 </div>
</template>

<script type="text/ecmascript-6">
  export default {
    name: "audios",
    data(){
      return{
        audioPlay:false
      }
    },
    methods:{
      autoPlay(){
        let audio = this.$refs.audio
        if(audio.paused){
          audio.play()
          this.audioPlay = true
        }else{
          audio.pause()
          this.audioPlay = false
        }
      },
    }
  }
</script>

<style lang="less" rel="stylesheet/less">
  @import "../../assets/animate.less";
  .audiosSwiper{
    background: transparent;
    position: fixed;
    right: 12px;
    top: 57%;
    z-index: 9;
    width: 35px;
    height: 35px;
    border-radius: 50%;
    cursor: pointer;
  }
  .audios,.audios1,.audios2{
    background: transparent;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 9;
    width: 35px;
    height: 35px;
    border-radius: 50%;
    cursor: pointer;
    &.dot{
      animation: sploosh 1.5s cubic-bezier(0.165, 0.84, 0.44, 1) infinite;
    }
    &.dot2 {
      animation: sploosh2 1.5s cubic-bezier(0.165, 0.84, 0.44, 1) infinite;
    }
    &.dot3 {
      animation: sploosh3 1.5s cubic-bezier(0.165, 0.84, 0.44, 1) infinite;
    }
    .btn{
      background: url("../../assets/redo.jpg") no-repeat;
      animation:move 5s linear infinite  ;
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      border:0;
      background-size: cover;
      color: transparent;
    }
    .fa{
      font-size: 16px;
      color: #fff;
      position: absolute;
      width: 35px;
      height: 35px;
      border-radius: 50%;
      text-align: center;
      line-height: 35px;
    }
  }
  @import "../../assets/media.less";
</style>



