<template>
  <div>
    <full-page ref="fullpage" :options="options" @onLeave="afterLoad">
      <v-page1></v-page1>
      <v-page2></v-page2>
      <v-page3></v-page3>
      <v-page4></v-page4>
      <v-page5></v-page5>
    </full-page>
    <v-audios></v-audios>
    <v-tab :refs="this.$refs"/>
    <v-back v-if="navToggle"></v-back>
  </div>

</template>

<script type="text/ecmascript-6">
  import tab from '../navTab/navTab'
  import page1 from './page1/page1'
  import page2 from './page2/page2'
  import page3 from './page3/page3'
  import page4 from './page4/page4'
  import page5 from './page5/page5'
  import back from '../back/back'
  import audios from '../audio/audio'
  import {mapState} from 'vuex'
  import {mapMutations} from 'vuex'
  export default {
    data() {
      return {
        options: {
          afterLoad: this.afterLoad,
          loopBottom:true,
          loopTop:true,
          css3:true,
          navigation:true,
          navigationTooltips:['1','2'],
          licenseKey: 'OPEN-SOURCE-GPLV3-LICENSE',
        }
      }
    },
    computed: {
      ...mapState([
        'navToggle',
      ]),
    },
    methods: {
      ...mapMutations([
        'swiperIndexAction'
      ]),
      afterLoad(origin, destination, direction){
        this.swiperIndexAction(destination.index)
      }
    },
    created(){

    },
    components:{
      "v-tab":tab,
      "v-page1": page1,
      "v-page2": page2,
      "v-page3": page3,
      "v-page5": page5,
      "v-audios": audios,
      "v-back": back,
      "v-page4": page4
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" rel="stylesheet/less">
  .fp-tableCell{
    display: block !important;
  }
  .section {
    width: 100%;
    height: 100%;
  }
  #fp-nav{
    z-index: 6 !important;
  }
</style>



